const Component = React.Component;
const Tooltip = require("rc-tooltip");
const Emitter = require('./emitter');

const LANG = {
    "zh-cn": {
        followTitle:"",
        eachFollowedTitle:"",
        cancelFollowedTitle:"",
        procesText:"",
        emailAddrText: "",
        extensionPhoneTitle:"",
        extensionPhoneText:"",
        aliwwText: "",
        errorTip: "",
        hangupPhoneCall: ""
    },
    "en": {
        followTitle: "Click to follow",
        eachFollowedTitle: "Mutual Friends. Click to unfollow.",
        cancelFollowedTitle: "Followed. Click to unfollow.",
        procesText: "Processing...",
        emailAddrText: "Email",
        extensionPhoneTitle: "Click to dial the number",
        extensionPhoneText: "Phone",
        aliwwText: "Wang",
        errorTip: "Sorry. No information found!",
        hangupPhoneCall: "Click to hang up"
    }
};

const DevicePixelRatio = 'devicePixelRatio' in window ? +window.devicePixelRatio : 1;
function getAvatarUrl(userInfo) {
    let size;

    if (DevicePixelRatio > 1) {
        size = '80x80';
    } else {
        size = '40x40';
    }

    if (userInfo.photoPath) {
        const host = userInfo.photoPath.indexOf('.tfsprivate') < 0 ? '//aliwork.alicdn.com/' : '//g01.alibaba-inc.com/';

        return host + userInfo.photoPath + '_' + size;
    } else if (userInfo.avatar) {
        var url = [userInfo.avatar.split('.jpg')[0], ('.'+size+'.jpg')].join('');
        if (url.indexOf('//') < 0) {
            url = 'https://work.alibaba-inc.com' + url;
        }
    } else {
        return '//aliwork.alicdn.com/tfscom/T1fxt7FtJlXXXXXXXX_' + size;
    }
}

class Avatar extends Component {
    render() {
        const userInfo = this.props.user.userInfo;
        return <div className="kuma-hovercard-avatar">
            <a href={"https://work.alibaba-inc.com/work/u/" + userInfo.emplId} target="_blank">
                <img src={getAvatarUrl(userInfo)} />
            </a>
        </div>;
    }
}

class UserDetail extends Component {
    render() {
        const userInfo = this.props.user.userInfo;

        let displayName = userInfo.name;
        if (userInfo.nickNameCn && this.props.locale !== 'en') {
            displayName += '(' + userInfo.nickNameCn + ')';
        }
        displayName += ' ' + (this.props.locale === 'en' ? ('(' + userInfo.emplId + ')') : (" " + userInfo.emplId));

        return <ul className="kuma-hovercard-content">
            <li title={displayName}>
                <a href={"https://work.alibaba-inc.com/work/u/" + userInfo.emplId} target="_blank" className="kuma-hovercard-name g-csscut-more">
                    {displayName}
                </a>
            </li>
            {this.props.hideJobDesc
                ? <li className="g-csscut-more singledetail" title={userInfo.deptDesc}>{userInfo.deptDesc}</li>
                : null}

            {this.props.hideJobDesc
                ? null
                : <li className="g-csscut-more" title={userInfo.jobDesc}>{userInfo.jobDesc}</li>}

            {this.props.hideJobDesc
                ? null
                : <li className="g-csscut-more" title={userInfo.deptDesc}>{userInfo.deptDesc}</li>}
        </ul>;
    }
}

class Connection extends Component {

    constructor(props) {
        super(props);

        this.user = this.props.user;

        this.state = {
            connection: this.user.getConnection()
        };

        const connectionchange = (connection) => {
            this.setState({connection});
        };

        this.user.on('connectionchange', connectionchange);

        this.stopListen = () => {
            this.user.off('connectionchange', connectionchange);
        };
    }

    componentWillUnmount() {
        this.stopListen && this.stopListen();
    }

    changeRelation() {
        if (!this.state.connection) {
            return;
        }
        if (this.state.connection === 'friends' || this.state.connection === 'bilateral') {
            this.user.breakup();
        } else {
            this.user.follow();
        }
    }

    render() {
        const lang = LANG[this.props.locale];
        let connection;
        if (this.state.connection === 'bilateral') {
            connection = <span className="kuma-hovercard-friends" title={lang.eachFollowedTitle} />;
        } else if (this.state.connection === 'following') {
            connection = <span className="kuma-hovercard-followed" title={lang.cancelFollowedTitle} />;
        } else if (this.state.connection === 'noncontact' || this.state.connection === 'followers') {
            connection = <span className="kuma-hovercard-stranger" title={lang.followTitle} />
        } else {
            connection = <span className="kuma-hovercard-connecting" title={lang.procesText} />
        }
        return <div className="kuma-hovercard-conn" onClick={this.changeRelation.bind(this)}>{connection}</div>
    }
}

class At extends Component {
    render() {
        return <li className="kuma-hovercard-at" onClick={() => {
            this.props.user.callAt();
        }} />;
    }
}

class PhoneCall extends Component {
    constructor(props) {
        super(props);

        this.user = this.props.user;

        this.state = {
            status: this.user.getPhoneStatus()
        };

        const phonestatus = (status) => {
            this.setState({status});
        };

        this.user.on('phonestatus', phonestatus);

        this.stopListen = () => {
            this.user.off('phonestatus', phonestatus);
        };
    }

    componentWillUnmount() {
        this.stopListen && this.stopListen();
    }

    onClick() {
        if (this.state.status === 'on') {
            this.user.hangExtPhone();
        } else {
            this.user.callExtPhone();
        }
    }

    render() {
        const lang = LANG[this.props.locale];

        return <li className={"kuma-hovercard-phone " + (this.state.status === 'on' ? 'kuma-hovercard-phoneon' : 'kuma-hovercard-phoneoff')}
                   title={this.state.status === 'on' ? lang.hangupPhoneCall : lang.extensionPhoneTitle}
                   onClick={this.onClick.bind(this)}>
            <span>{lang.extensionPhoneText}</span>
        </li>
    }
}

class WangWang extends Component {
    render() {
        const lang = LANG[this.props.locale];

        return <li className="kuma-hovercard-ww" onClick={() => {
            this.props.user.callWangWang()
        }}><span>{lang.aliwwText}</span></li>
    }
}

class Mail extends Component {
    render() {
        const lang = LANG[this.props.locale];

        return <li className="kuma-hovercard-email" onClick={() => {
            this.props.user.callMail();
        }} ><span>{lang.emailAddrText}</span></li>;
    }
}

class Tools extends Component {
    render() {
        return <div className="kuma-hovercard-tools">
            <ul>
                <At user={this.props.user} locale={this.props.locale} />
                {this.props.user.hasExtPhone() ? <PhoneCall user={this.props.user} locale={this.props.locale} /> : null}
                {this.props.user.hasWW() ? <WangWang user={this.props.user} locale={this.props.locale} /> : null}
                {this.props.user.hasMail() ? <Mail user={this.props.user} locale={this.props.locale} /> : null}
            </ul>
        </div>;
    }
}

class User extends Emitter {
    constructor(params, userInfo) {
        super();
        this.locale = params.locale;
        this.token = params.token;
        this.params = params;
        this.ready = 0;
        if (userInfo) {
            this.userInfo = userInfo;
            this.ready = 1;
        } else {
            this.userInfo = {emplId:params.emplId};
        }
    }

    prepare() {
        if (this.prepared) return;
        this.prepared = 1;

        if (this.ready) {
            this.wangwangStatus();
        } else {
            this.fetchUserInfo();
        }
    }

    getConnection() {
        return this.userInfo.connection;
    }

    getPhoneStatus() {
        return this.phoneStatus || 'off';
    }

    hasExtPhone() {
        return this.userInfo.extensionPhone != null && this.userInfo.extensionPhone !== '';
    }

    hasWW() {
        return this.userInfo.tbWW || this.userInfo.aliWW;
    }

    hasMail() {
        return this.userInfo.emailAddr != null && this.userInfo.emailAddr !== '';
    }

    isReady() {
        return this.ready > 0;
    }

    isValid() {
        return this.userInfo.emplId != null && this.userInfo.emplId !== '' && this.userInfo.name != null && this.userInfo.name !== '';
    }

    getAccessToken () {
        const tokenSource = this.params.token;
        if (typeof tokenSource == 'function') {
            return tokenSource();
        } else {
            return tokenSource;
        }
    }

    callOpenAPI(api, params) {
        if (!params) {
            params = {};
        }
        params['_'] = +new Date;
        params['accessToken'] = this.getAccessToken();

        return jQuery.ajax({
            url: "https://work.alibaba-inc.com" + api + '?' + jQuery.param(params),
            dataType: "jsonp",
            timeout: 5000
        });
    }

    fetchUserInfo() {
        const params = {
            searchEmplId: this.params.emplId,
            lang: this.params.locale
        };
        if (this.params.extra) {
            for (let k in this.params.extra) {
                if (this.params.extra.hasOwnProperty(k) && !params[k]){
                    params[k] = this.params.extra[k];
                }
            }
        }
        this.callOpenAPI("/work/xservice/open/api/v1/relation/getUserConnection.jsonp", params).done((res) => {
            try {
                const data = res.content.userInfo;
                data.loginId = res.content.loginUserId;
                data.connection = res.content.connection || false;

                this.userInfo = data;
            } catch (e) {
                this.userInfo = {};
            }
            this.ready = 1;
            this.wangwangStatus();
            this.emit('ready', this.userInfo);
        });
    }

    follow() {
        this.lastConnection = this.userInfo.connection;
        this.userInfo.connection = null;
        this.emit('connectionchange', null);
        this.callOpenAPI("/work/xservice/open/api/v1/relation/followPerson.jsonp", {"emplId": this.userInfo.emplId}).done((res) => {
            this.userInfo.connection = res.content;
            this.emit('connectionchange', res.content);
        }).fail(() => {
            this.userInfo.connection = this.lastConnection;
            this.emit('connectionchange', this.lastConnection);
        });
    }

    breakup() {
        this.lastConnection = this.userInfo.connection;
        this.userInfo.connection = null;
        this.emit('connectionchange', null);
        this.callOpenAPI("/work/xservice/open/api/v1/relation/removeMyFollowing.jsonp",
            {"followerEmplId": this.userInfo.loginId, "followeeEmplId": this.userInfo.emplId}).done((res) => {
                // followers
            this.userInfo.connection = res.content;
            this.emit('connectionchange', res.content);
        }).fail(() => {
            this.userInfo.connection = this.lastConnection;
            this.emit('connectionchange', this.lastConnection);
        });
    }

    hangExtPhone() {
        if (!this.phoneCallUuid) return;

        this.callOpenAPI("/work/xservice/open/api/v1/common/stopPhoneCall.jsonp", {"uuid": this.phoneCallUuid}).done((res) => {
            if (res.content && (res.content === 'ok' || res.content === 'uuid:' + this.phoneCallUuid)) {
                this.phoneCallUuid = null;
                this.phoneStatus = 'off';
                this.emit('phonestatus', this.phoneStatus);
            }
        });
    }

    callMail() {
        top.location.href = "mailto:" + this.userInfo.emailAddr;
    }

    callExtPhone() {
        this.callOpenAPI("/work/xservice/open/api/v1/common/makePhoneCall.jsonp", {"number": this.userInfo.extensionPhone, "isMobileNumber": false})
        .done((res) => {
            if (res.content && res.content.substr(0, 4) === 'uuid') {
                this.phoneCallUuid = res.content.substr(5);
                this.phoneStatus = 'on';
                this.emit('phonestatus', this.phoneStatus);
            }
        });
    }

    callAt() {
        let who = '('+ this.userInfo.emplId +')';
        if (this.userInfo.nickNameCn) {
            who = '@' + this.userInfo.nickNameCn + who;
        } else {
            who = '@' + this.userInfo.name + who;
        }
        const win = window.open("https://work.alibaba-inc.com/work/home?privateat=" + who, '_blank');
        win.focus();
    }

    callWangWang() {
        const preferAccount = this.getPreferWWAccount();
        if (!preferAccount) return;

        const siteid = preferAccount.substr(0, 8) === 'cnalichn' ? 'cntaobao' : 'cnalichn';
        const wwlink = 'aliim:sendmsg?touid=' + preferAccount + '&siteid=' + siteid + '&fenliu=1&status=2';

        top.location.href = wwlink;
    }

    getPreferWWAccount() {
        if (this.preferAccount) {
            return this.preferAccount;
        }
        return this.getWWUids()[0] || '';
    }

    getWWUids() {
        const uids = [];
        if (this.userInfo.tbWW) {
            uids.push('cntaobao' + this.userInfo.tbWW);
        }
        if (this.userInfo.aliWW) {
            uids.push('cnalichn' + this.userInfo.aliWW);
        }
        return uids;
    }

    wangwangStatus() {
        const uids = this.getWWUids();
        jQuery.ajax({
            url: '//amos.alicdn.com/mullidstatus.aw',
            dataType: 'jsonp',
            data: {
                "charset": "utf-8",
                "uids": uids.join(';')
            }
        }).done((res) => {
            let preferAccount = uids[0] || '';
            const data = res.data;
            for (let i = 0, l = data.length; i < l; i++) {
                if (data[i] > 0) {
                    preferAccount = uids[i];
                    break;
                }
            }
            this.preferAccount = preferAccount;
        });
    }
}

const UserCache = {};

function getUser(props) {
    const emplId = props.userInfo && props.userInfo.emplId || props.emplId;

    if (emplId && UserCache.hasOwnProperty(emplId)) {
        return UserCache[emplId];
    }

    const params = {
        emplId: emplId,
        token: props.token,
        locale: props.locale
    };

    const user = new User(params, props.userInfo);

    if (emplId) {
        UserCache[emplId] = user;
    }

    return user;
}

class Hovercard extends Component {

    constructor(props) {
        super(props);

        this.user = getUser(this.props);

        const ready = () => {
            this.setState({});
        };
        this.user.on('ready', ready);
        this.stopListen = () => {
            this.user.off('ready', ready);
        };

        this.state = {};
    }

    componentWillUnmount() {
        this.stopListen && this.stopListen();
    }

    generateOverlay() {
        const lang = LANG[this.props.locale];

        if (!this.user.isReady()) {
            return null;
        }

        if (!this.user.isValid()) {
            return <div className="kuma-hovercard-container alinw-hovercard">
                <div className="kuma-hovercard-error">{lang.errorTip}</div>
            </div>;
        }

        return <div className="kuma-hovercard-container alinw-hovercard">
            <div className="kuma-hovercard-info">
                <Avatar user={this.user} locale={this.props.locale} />
                <UserDetail user={this.user} hideJobDesc={this.props.hideJobDesc} locale={this.props.locale} />
                {this.props.showConn ? <Connection user={this.user} locale={this.props.locale} /> : null}
            </div>
            <Tools user={this.user} locale={this.props.locale} />
        </div>;
    }

    onVisibleChange(visible) {
        if (visible) {
            this.user.prepare();
        }
    }

    render() {
        const placement = this.props.placement || {points: ['tl', 'bl'], overflow:{adjustX:true,adjustY:true}};
        return (
            <Tooltip prefixCls="kuma-hovercard" trigger="hover" delay={this.props.showDelay}
                     onVisibleChange={this.onVisibleChange.bind(this)} overlay={this.generateOverlay()}
                     placement={placement}>
                {this.props.children}
            </Tooltip>
        );
    }
}

Hovercard.defaultProps = {
    showDelay: 0.1,
    hideJobDesc: false,
    showConn: true,
    token: '',
    emplId: '',
    userInfo: null,
    locale: 'zh-cn',
    placement: null
};

// http://facebook.github.io/react/docs/reusable-components.html
Hovercard.propTypes = {
    token: React.PropTypes.oneOfType([
        React.PropTypes.string,
        React.PropTypes.func
    ]),
    emplId: React.PropTypes.string,
    userInfo: React.PropTypes.object,
    hideJobDesc: React.PropTypes.bool,
    showConn: React.PropTypes.bool,
    showDelay: React.PropTypes.number,
    locale: React.PropTypes.oneOf(['zh-cn', 'en']),
    placement: React.PropTypes.oneOf(['top', 'bottom', 'left', 'right'])
};

Hovercard.displayName = "Hovercard";

module.exports = Hovercard;



